/*=======================START-MEDIA=====================*/
/*=======================END-MEDIA=====================*/
/* media breakpoint mixin */
.hero__info-wrapper {
  display: flex;
  flex-direction: column;
}

@media only screen and (min-width: 1200px) {
  .hero__info-wrapper {
    flex-direction: row;
  }
}

.info-box {
  max-width: 315px;
  margin-left: 144px;
}

.info-box__button, .info-box__text {
  margin-top: 39px;
}

.hero {
  padding-top: 118px;
  padding-bottom: 48px;
  position: relative;
  z-index: 1;
}

@media only screen and (min-width: 1200px) {
  .hero {
    padding-top: 109px;
  }
}

.hero__icon {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  z-index: 2;
}

@media only screen and (max-width: 1420px) {
  .hero__icon {
    width: 100%;
    height: auto;
  }
}

.hero__info-wrapper {
  gap: 28px;
  margin-top: 217px;
  max-width: 470px;
}

@media only screen and (min-width: 1200px) {
  .hero__info-wrapper {
    gap: 72px;
    margin-top: 63px;
  }
}

.hero__info {
  flex-grow: 1;
}

.hero__button {
  margin-top: 28px;
}

@media only screen and (min-width: 1200px) {
  .hero__button {
    margin-top: 41px;
  }
}

.hero__decor {
  position: absolute;
  z-index: 1;
}

@media only screen and (max-width: 1200px) {
  .hero__decor {
    height: auto;
  }
}

.hero__decor-top-right {
  left: calc(50% - 63px);
  top: -144px;
}

@media only screen and (max-width: 1200px) {
  .hero__decor-top-right {
    width: 196px;
  }
}

@media only screen and (min-width: 1200px) {
  .hero__decor-top-right {
    left: calc(50% + 133px);
    top: -224px;
  }
}

.hero__decor-top-left {
  left: calc(50% - 190px);
  top: -41px;
  z-index: 3;
}

@media only screen and (max-width: 1200px) {
  .hero__decor-top-left {
    width: 188px;
  }
}

@media only screen and (min-width: 1200px) {
  .hero__decor-top-left {
    left: calc(50% - 105px);
    top: -6px;
  }
}

.hero__decor-center {
  top: 41px;
  left: calc(50% + 12px);
}

@media only screen and (max-width: 1200px) {
  .hero__decor-center {
    width: 174px;
  }
}

@media only screen and (min-width: 1200px) {
  .hero__decor-center {
    left: calc(50% + 146px);
    top: 133px;
  }
}

.hero__decor-bottom {
  top: 146px;
  left: calc(50% - 208px);
}

@media only screen and (max-width: 1200px) {
  .hero__decor-bottom {
    width: 352px;
  }
}

@media only screen and (min-width: 1200px) {
  .hero__decor-bottom {
    left: calc(50% - 280px);
    top: 319px;
  }
}

.about {
  padding-top: 163px;
  display: flex;
  flex-direction: column;
}

.about__icon {
  margin-top: auto;
}

@media only screen and (min-width: 1200px) {
  .about__icon-mobile {
    display: none;
  }
}

@media only screen and (max-width: 1200px) {
  .about__icon-desktop {
    display: none;
  }
}

.section-decor {
  position: relative;
  z-index: 11;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

.section-decor__wrapper {
  flex-grow: 1;
  width: 100%;
}

.section-decor__bg, .section-decor__img {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.section-decor__bg {
  width: 100%;
  height: 100vh;
}

.section-decor__img {
  height: auto;
}

.details {
  position: relative;
}

.details__list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 70px;
  margin-top: 145px;
  min-width: 430px;
}

.details__item {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  flex-basis: calc((100% - 70px) / 2);
}

.details__item svg {
  flex-shrink: 0;
}

.details__img {
  position: absolute;
  right: 0;
  top: 0;
}

.details__container {
  position: relative;
}
